import '../styles.scss'
import { installManifest, plistMap, platformList } from './constants'

export const downloadAppBtn = document.querySelector('#download-app-btn')

export const header = document.querySelector('.header')
export const title = document.querySelector('.header__title')
export const logo = document.querySelector('.header__logo')
export const subTitle = document.querySelector('.header__subtitle')
export const sardField = document.querySelector('.card-field')

window.addEventListener('scroll', () => {
  if (window.scrollY >= 8) {
    if (!header.classList.contains('header--compact'))
      header.classList.add('header--compact')
    if (!logo.classList.contains('header__logo--hidden'))
      logo.classList.add('header__logo--hidden')
    if (!title.classList.contains('header__title--compact'))
      title.classList.add('header__title--compact')
    if (!subTitle.classList.contains('header__subtitle--compact'))
      subTitle.classList.add('header__subtitle--compact')
    if (!sardField.classList.contains('card-field--compact'))
      sardField.classList.add('card-field--compact')
  } else {
    if (header.classList.contains('header--compact'))
      header.classList.remove('header--compact')
    if (logo.classList.contains('header__logo--hidden'))
      logo.classList.remove('header__logo--absolute', 'header__logo--hidden')
    if (title.classList.contains('header__title--compact'))
      title.classList.remove('header__title--compact')
    if (subTitle.classList.contains('header__subtitle--compact'))
      subTitle.classList.remove('header__subtitle--compact')
    if (sardField.classList.contains('card-field--compact'))
      sardField.classList.remove('card-field--compact')
  }
})

const plist =
  plistMap[new URLSearchParams(window.location.search).get('plistId')]

const platformIsAvailable = platformList.includes(
  navigator.userAgentData.platform
)

const startInstall = () => {
  if (plist) {
    window.location.href = installManifest + encodeURIComponent(plist)
  }

  if (!platformIsAvailable) {
    alert('Устройство не поддерживается')
  }
}
downloadAppBtn.addEventListener('click', startInstall)

if (plist) {
  subTitle.innerHTML = '«РСХБ ДБО ФЛ Учёт надоя» на iphone'
} else {
  downloadAppBtn.setAttribute('disabled', true)

  subTitle.innerHTML = 'Ссылка на «РСХБ ДБО ФЛ Учёт надоя» не найдена'
}

window.addEventListener('load', () => {
  downloadAppBtn.click()
})
